@import url(/src/styles/accessories.css);

* {
  margin: 0px;
  padding: 0px;
}

body {
	background-color: var(--background-color);
	color: var(--font-color);
	font-family: var(--text-font);
}

h1, h2 {
	text-align: center;
    margin: 15px;
	font-family: var(--heading-font);
}

h3 {
	font-family: var(--heading-font);
}


/* === BUTTONS === */
.btns {
	display: flex;
	justify-content: center;
	align-items: center;
	margin-bottom: 15px;
}

.button {
	padding: 10px;
    border: solid 2px var(--background-color);
    border-radius: 10px;
    margin: 0px 5px;
    cursor: pointer;
    white-space: nowrap;
    transition: 0.2s;
	text-decoration: none;
	font-family: var(--heading-font);
}


/* === IMAGES === */
div.image-container {
	max-height: 300px;
	min-height: 200px;
	overflow: hidden;
	margin: 0 auto;
	display: flex;
	align-items: center;
}

div.image-container img.image {
	width: 100%;
	min-height: 200px;
}


/* === SCROLLBAR === */
::-webkit-scrollbar {
	width: 12px;
}
  
::-webkit-scrollbar-track {
	background: var(--background-color);
}
  
::-webkit-scrollbar-thumb {
	background: var(--primary);
	border-radius: 15px;
	border: 3px solid var(--background-color);
}


/* NOTFOUND PAGE (404) */
.not-found {
	display: flex;
	flex-direction: column;
}

.not-found-text {
    margin: 25px;
}

.button.not-found-button {
	margin: 0 25px;
	background-color: var(--info);
	color: var(--font-color);
	text-align: center;
}

.button.not-found-button:hover {
	background-color: var(--background-color);
	border: 2px solid var(--info);
}


/* ===MISC === */
.hidden {
	display: none;
}

.loading {
	transform: scale(1);
	animation: pulse 1s infinite;
}

.no-thoughts {
	padding: 25px;
}

.no-thoughts > h3 {
	margin-bottom: 25px;
}



/* === ANIMATIONS === */
@keyframes pulse {
	0% {
		opacity: 1;
        transform: scale(1);
	}
	50% {
		opacity: 0.6;
        transform: scale(0.998);
	}
	100% {
		opacity: 1;
        transform: scale(1);
	}
}


/* === MEDIA QUERIES === */
@media only screen and (min-width: 480px){
	.thought {
		width: 430px;
	}

	.thought-container {
		justify-content: center;
		align-items: center;
	}

	.section-heading {
		text-align: center;
	}

	.home-thought-display {
		justify-content: center;
		align-items: center;
	}

	.delete-container {
		display: flex;
		flex-direction: column;
	}

	.delete-container > div.thought {
		justify-self: center;
		align-self: center;
	}

	.loading-container {
		display: flex;
		flex-direction: column;
		justify-content: center;
		align-items: center;
	}
}

@media only screen and (min-width: 768px){
	.flex {
		display: flex;
		flex-direction: column;		
	}

	.about-text {
		max-width: 700px;		
		justify-self: center;
		align-self: center;
	}

	.thought {
		width: 500px;
	}

	form.form {
		width: 750px;
	}

	.form-container {
		display: flex;
		flex-direction: column;
		justify-content: center;
		align-items: center;
	}

	main.not-found {
		display: flex;
		flex-direction: column;
		justify-content: center;
		align-items: center;
	}
}

@media only screen and (min-width: 960px){
	div.image-container {
		max-height: 500px;
	}

	.thought-container-search > form > input {
		width: 300px;
	}
}

@media only screen and (min-width: 1200px){
	.home-section-flex {
		display: flex;
		justify-content: space-evenly;
	}

	.about-text {
		max-width: 900px;		
	}

	.thought-container-display {
		display: grid;
		grid-template-columns: 600px 600px;
		place-items: center;
	}

	.thought-container-search > form > input {
		width: 400px;
	}
}

@media only screen and (min-width: 1500px){
	.about-text {
		max-width: 1000px;		
	}

	.thought {
		width: 600px;
	}

	.thought-container-display {
		display: grid;
		grid-template-columns: 700px 700px;
		place-items: center;
	}
}
